import { Accordion, Card, Col, Form, Button } from "react-bootstrap";
import { BsPencilSquare } from "react-icons/bs";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { useForm } from "react-hook-form";
import {
  deletePatientHistory,
  getPatientHistory,
  updatePatientHistory,
} from "../../../../app/features/patientHistory/patientHistory.slice";
import { useDispatch, useSelector } from "react-redux";
import { useState, useEffect } from "react";
import { ConfirmationModal } from "../../../../components/Modal/ConfirmationModal";
import {
  getAllPatientPharmacy,
  getPatientPharmacyData,
} from "../../../../app/features/patientPharmacy/patientPharmacy.slice";

function PatientMedicalHistory(props) {
  const { register, handleSubmit, reset } = useForm();
  const { user, getPatientData } = props;
  const dispatch = useDispatch();
  const [historyType, setHistoryType] = useState("");
  const [deleteModel, setDeleteModel] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const [medicalHistory, setmedicalHistory] = useState("");
  const [editing, setEditing] = useState(false);

  const { patientHistoryData } = useSelector((state) => state.patientHistory);

  /** Medical History Patient */
  const moveToNextMedicalHistory = () => {
    setmedicalHistory("");
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  };

  function onSubmitMedicalHistory(data) {
    const finalData = {
      patientId: user?.userId,
      historyTypeId:
        historyType === 1
          ? 1
          : historyType === 2
          ? 2
          : historyType === 3
          ? 3
          : null,
      pastMedicalHistory: data?.pastMedicalHistory,
      currentMedical: data?.currentMedical,
      allergyHistory: data?.allergyHistory,
    };
    dispatch(updatePatientHistory({ finalData, moveToNextMedicalHistory }));
  }

  /** Delete Medical */
  const moveToNextDeleteMedicalHistory = () => {
    setmedicalHistory("");
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
    reset({
      pastMedicalHistory: "",
    });
  };
  const onDelete = () => {
    const finalData = {
      patientId: user?.userId,
      historyTypeId: deleteId ? deleteId : null,
    };
    dispatch(
      deletePatientHistory({ finalData, moveToNextDeleteMedicalHistory })
    );
  };
  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);

  useEffect(() => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  return (
    <Col lg={12}>
      <div>
        <Card>
          <Card.Img variant="top" className="Card-Image" />
          <Card.Body className="p-0">
            <div className="d-flex align-items-center upload_pic">
              <img
                src={
                  getPatientData?.imageUrl
                    ? getPatientData?.imageUrl
                    : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=6045eb&color=fff`
                }
                alt=""
                srcset=""
                className="img-fluid Profile-2 position-relative test-border"
              />
            </div>
            <Card.Title className="Personal-Information mb-5 ps-3 fw-bold">
              Medical History
            </Card.Title>
            <Form onSubmit={handleSubmit(onSubmitMedicalHistory)}>
              <Accordion className="px-4" defaultActiveKey="0">
                <Accordion.Item eventKey="0" className="my-3 ">
                  <Accordion.Header>
                    <h4>Past Medications</h4>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientHistoryData?.pastMedicalHistory &&
                    editing === false ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.pastMedicalHistory}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(1);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="pastMedicalHistory"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.pastMedicalHistory ?? ""
                              }
                              rows={12}
                              {...register("pastMedicalHistory")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(1)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>

              <Accordion className="px-4" defaultActiveKey="1">
                <Accordion.Item eventKey="0" className="my-3">
                  <Accordion.Header>
                    <h4>Current Medications</h4>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientHistoryData?.currentMedical && editing === false ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.currentMedical}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(2);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="currentMedical"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.currentMedical ?? ""
                              }
                              rows={12}
                              {...register("currentMedical")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(2)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>

              <Accordion className="px-4" defaultActiveKey="2">
                <Accordion.Item eventKey="0" className="my-3">
                  <Accordion.Header>
                    <h4>Allergy (with any medication)</h4>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientHistoryData?.allergyHistory && editing === false ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.allergyHistory}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(3);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="allergyHistory"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.allergyHistory ?? ""
                              }
                              rows={12}
                              {...register("allergyHistory")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(3)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            </Form>
          </Card.Body>
        </Card>
        {deleteId === 1 ? (
          <ConfirmationModal
            show={deleteModel}
            onHide={() => setDeleteModel(false)}
            heading={"Delete Past Medical History"}
            title={"this Past Medical History"}
            removeFunc={onDelete}
          />
        ) : deleteId === 2 ? (
          <ConfirmationModal
            show={deleteModel}
            onHide={() => setDeleteModel(false)}
            heading={"Delete Current Medical History"}
            title={"this Current Medical History"}
            removeFunc={onDelete}
          />
        ) : (
          <ConfirmationModal
            show={deleteModel}
            onHide={() => setDeleteModel(false)}
            heading={"Delete Allergy History"}
            title={"this Allergy History"}
            removeFunc={onDelete}
          />
        )}
      </div>
    </Col>
  );
}

export default PatientMedicalHistory;
