import moment from "moment";

function EmergencyLetterPDF({ stateData }) {

  const currentDate = moment().format("DD/MM/YYYY");

  return (
    <div className="mb-5">
      <div className="text-center">
        <h1>FamilyDoc247</h1>
        <h6>General Practitioner (GP) / Family Physician</h6>
        <h6>Fitzwilliam Hall, Fitzwilliam Place, Dublin 2</h6>
        <h6>Tel: +353 1 906 9327</h6>
        <h6>Email : contact@familydoc247.ie</h6>
      </div>
      <p className="my-5">{currentDate}</p>
      <p className="mb-5">
        <span className="font-weight-600">To: </span>
        Emergency Department Doctor On Duty
      </p>
      <div>
        <p>
          Re: {stateData?.patient},{" "}
          {stateData?.currentAddress}
        </p>
        <p>
          DOB:{" "}
          {moment(stateData?.dob).format(
            "MM/DD/YYYY"
          )}
          , Tel:{" "}
          {stateData?.phoneNumber
            ? stateData?.phoneNumber
            : "N/A"}
        </p>
      </div>
      <p className="mt-5">Dear Doctor On Duty,</p>
      <p>I would appreciate if you could see above-named patient.</p>
      <div className="mb-5">
        {stateData?.emergenceyDepartmentLetter?.referralNote && (
          <div>
            <p className="text-black-50 mb-0">Referral Notes</p>
            {stateData?.emergenceyDepartmentLetter?.referralNote}
          </div>
        )}
        <div className="mb-3">
          <p className="text-black-50 mb-0">Presenting Complaint</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.presentingComplaint}
        </div>
        {/* <div className="mb-3">
          <p className="text-black-50 mb-0">History of presenting complaint</p>
          {stateData?.appointmentDetail?.historyOfPresentingComplaint}
        </div> */}
        <div className="mb-3">
          <p className="text-black-50 mb-0">Past Medical History</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.pastMedicalHistory}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Medications</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.medications}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Allergies</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.allergies}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Family History</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.familyHistory}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Social History</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.socialHistory}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Suspected Diagnosis</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.suspectedDiagnosis}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Recommended Actions</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.recommendedActions}
        </div>
        <div className="mb-3">
          <p className="text-black-50 mb-0">Sign off</p>
          {stateData?.emergenceyDepartmentLetter?.appointmentDetail?.signOff}
        </div>
      </div>
      <p className="pb-3">
        I would appreciate your urgent assessment and management.
      </p>
      <p className="pb-2">Yours sincerely,</p>
      <p className="mb-0">Dr {stateData?.doctor ? stateData?.doctor : "N/A"}</p>
      <p>
        MCRN{" "}
        {stateData?.mcrn
          ? stateData?.mcrn
          : "N/A"}
      </p>
    </div>
  );
}

export default EmergencyLetterPDF;
