import React, { useRef, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BsEye, BsEyeSlash } from "react-icons/bs";
import { useForm } from "react-hook-form";
import { changePassword } from "../../../../app/features/auth/auth.slice";
import { useDispatch } from "react-redux";

function PatientChangePassword(props) {
  const { getPatientData } = props;
  const dispatch = useDispatch();
  const [oldPasswordShown, setOldPasswordShown] = useState(false);
  const [passwordShown, setPasswordShown] = useState(false);
  const [confirmPasswordShown, setConfirmPasswordShown] = useState(false);

  const {
    register,
    watch,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const toggleOldPasswordVisiblity = () => {
    setOldPasswordShown(oldPasswordShown ? false : true);
  };

  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };

  const toggleConfirmPasswordVisiblity = () => {
    setConfirmPasswordShown(confirmPasswordShown ? false : true);
  };
  const password = useRef({});
  password.current = watch("password", "");

  const moveToNext = () => {
    reset({
      oldPassword: "",
      password: "",
      confirmPassword: "",
    });
  };
  function onSubmitConfirmPassword(data) {
    const finalData = {
      password: data.oldPassword,
      newPassword: data.password,
    };
    dispatch(changePassword({ finalData, moveToNext }));
  }
  return (
    <Col lg={12}>
      <div>
        <Card>
          <Card.Img variant="top" className="Card-Image" />
          <Card.Body className="p-0">
            <div className="d-flex align-items-center upload_pic">
              <img
                src={
                  getPatientData?.imageUrl
                    ? getPatientData?.imageUrl
                    : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=6045eb&color=fff`
                }
                alt=""
                srcset=""
                className="img-fluid Profile-2 position-relative test-border"
              />
            </div>

            <Card.Title className="Personal-Information mb-5 fw-bold">
              Change Password
              <p
                className="Password-text mt-3 mb-3"
                style={{ color: "#999999" }}
              >
                Use a strong password. Don’t use a password from another sites,
                or something too obvious like your pet’s name.
              </p>
            </Card.Title>
            <Form
              className="mt-4"
              onSubmit={handleSubmit(onSubmitConfirmPassword)}
            >
              <Row className="mt-4 Password-Input-Spacing">
                <Col lg={6} md={6} sm={6} xs={10}>
                  <Form.Group
                    className="mb-3 position-relative"
                    controlId="formBasicEmail"
                  >
                    <Form.Label
                      className="fw-bold Form-label-Font"
                      style={{ color: "#1A1A1A" }}
                    >
                      Old Password
                    </Form.Label>
                    <Form.Control
                      type={oldPasswordShown ? "text" : "password"}
                      placeholder="Old Password"
                      className="Field-Sizing"
                      name="oldPassword"
                      {...register("oldPassword", {
                        required: true,
                      })}
                    />
                    {oldPasswordShown === false ? (
                      <BsEyeSlash
                        className=" Password-Eye-Icon "
                        onClick={toggleOldPasswordVisiblity}
                      />
                    ) : (
                      <BsEye
                        className=" Password-Eye-Icon "
                        onClick={toggleOldPasswordVisiblity}
                      />
                    )}
                    {errors.oldPassword && (
                      <p className="text-danger">
                        {errors.oldPassword.message}
                      </p>
                    )}
                  </Form.Group>
                  <Form.Group
                    className="mb-3 position-relative"
                    controlId="formBasicEmail"
                  >
                    <Form.Label
                      className="fw-bold Form-label-Font"
                      style={{ color: "#1A1A1A" }}
                    >
                      New Password
                    </Form.Label>
                    <Form.Control
                      type={passwordShown ? "text" : "password"}
                      placeholder="New Password"
                      className="Field-Sizing"
                      name="password"
                      {...register("password", {
                        required: true,
                        //   pattern:
                        //     /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
                      })}
                    />
                    {passwordShown === false ? (
                      <BsEyeSlash
                        className=" Password-Eye-Icon "
                        onClick={togglePasswordVisiblity}
                      />
                    ) : (
                      <BsEye
                        className=" Password-Eye-Icon "
                        onClick={togglePasswordVisiblity}
                      />
                    )}
                    {errors.password && (
                      <p className="text-danger">{errors.password.message}</p>
                    )}
                  </Form.Group>
                  <Form.Group
                    className="mb-3 position-relative"
                    controlId="formBasicEmail"
                  >
                    <Form.Label
                      className="fw-bold Confirm-Password Form-label-Font"
                      style={{ color: "#1A1A1A" }}
                    >
                      Confirm Password
                    </Form.Label>
                    <Form.Control
                      type={confirmPasswordShown ? "text" : "password"}
                      name="confirmPassword"
                      placeholder="Confirm New Password"
                      className="Field-Sizing"
                      {...register("confirmPassword", {
                        validate: (value) =>
                          value === password.current ||
                          "The passwords does not match",
                      })}
                    />
                    {confirmPasswordShown === false ? (
                      <BsEyeSlash
                        className=" Password-Eye-Icon "
                        onClick={toggleConfirmPasswordVisiblity}
                      />
                    ) : (
                      <BsEye
                        className=" Password-Eye-Icon "
                        onClick={toggleConfirmPasswordVisiblity}
                      />
                    )}
                    {errors.confirmPassword && (
                      <p className="text-danger">
                        {errors.confirmPassword.message}
                      </p>
                    )}
                  </Form.Group>
                  <div className="d-grid gap-2">
                    <Button
                      type="submit"
                      value="Submit"
                      variant="primary"
                      size="lg"
                      className="Save-password-button my-2"
                    >
                      Save Password
                    </Button>
                  </div>
                </Col>
              </Row>
            </Form>
          </Card.Body>
        </Card>
      </div>
    </Col>
  );
}

export default PatientChangePassword;
