import { useEffect, useRef, useState } from "react";
import {
  Accordion,
  Card,
  Col,
  Dropdown,
  DropdownButton,
} from "react-bootstrap";
import { BsThreeDotsVertical } from "react-icons/bs";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrintComponents from "react-print-components";
import { useDispatch } from "react-redux";
import { getDoctorAppointmentDetail } from "../../../../app/features/appointment/appointment.slice";
import moment from "moment";
import MedicalCertificatePDF from "../../../../components/Theme/MedicalCertificatePDF";
import SpecialistLetterPDF from "../../../../components/Theme/ReferralNotesPDF/SpecialistLetter";
import EmergencyLetterPDF from "../../../../components/Theme/ReferralNotesPDF/EmergencyLetter";

function PatientReferrelNote(props) {
  const { profileNotesData, getPatientData } = props;
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const [singleDownLoad, setSingleDownload] = useState(null);
  const [emergencyData, setEmergencyData] = useState(null);
  const [medicalCertificateData, setMedicalCertificateData] = useState(null);

  const printButtonRef = useRef(null);
  const printButtonEmerRef = useRef(null);
  const printButtonMedRef = useRef(null);

  const dispatch = useDispatch();

  // const handleSpecialityFn = (data) => {
  //   setSingleDownload(data);
  //   printButtonRef?.current?.click();
  // };

  const handleSpecialityFn = (data) => {
    setSingleDownload(data);
  };
  const handleEmergencyFn = (data) => {
    setEmergencyData(data);
  };
  const handleMedicalFn = (data) => {
    setMedicalCertificateData(data);
  };

  useEffect(() => {
    if (singleDownLoad) {
      printButtonRef?.current?.click();
      setSingleDownload(null);
    } else if (emergencyData) {
      printButtonEmerRef?.current?.click();
      setEmergencyData(null);
    } else if (medicalCertificateData) {
      printButtonMedRef?.current?.click();
      setMedicalCertificateData(null);
    }
  }, [singleDownLoad, emergencyData, medicalCertificateData]);

  useEffect(() => {
    const finalData = {
      doctorId: slotDescription?.doctorId,
    };
    dispatch(getDoctorAppointmentDetail(finalData));
  }, [dispatch, slotDescription?.doctorId]);

  return (
    <Col lg={12}>
      <div className="referrel-note pt-ref-notes">
        <Card>
          <Card.Img variant="top" className="Card-Image" />
          <Card.Body className="p-0">
            <div className="d-flex align-items-center upload_pic">
              <img
                src={
                  getPatientData?.imageUrl
                    ? getPatientData?.imageUrl
                    : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=6045eb&color=fff`
                }
                alt=""
                srcset=""
                className="img-fluid Profile-2 position-relative test-border"
              />
            </div>

            <Card.Title className="Personal-Information mb-5 ps-3 fw-bold">
              Referral Notes
            </Card.Title>
            <div className="referrel-note-data">
              {profileNotesData?.length > 0 ? (
                profileNotesData?.map((data, index) => {
                  return (
                    <>
                      <Accordion className="mb-3 mx-4">
                        <Accordion.Item eventKey="index">
                          <Accordion.Header>
                            <div className="d-flex justify-content-between fs-6 w-100">
                              <div>
                                <span className="d-flex">
                                  <p className="fw-bold pe-1">Doctor Name: </p>
                                  <p>{data?.doctor}</p>
                                </span>
                                <span className="d-flex">
                                  <p className="mb-0 fw-bold pe-1">
                                    Start Time:{" "}
                                  </p>
                                  <p className="mb-0">{data?.startTime}</p>
                                </span>
                              </div>
                              <div className="me-4">
                                <span className="d-flex">
                                  <p className="fw-bold pe-1">
                                    Appointment Date:{" "}
                                  </p>
                                  <p>
                                    {moment(data?.appointmentDate)?.format(
                                      "DD-MM-YYYY"
                                    )}
                                  </p>
                                </span>
                                <span className="d-flex">
                                  <p className="mb-0 fw-bold pe-1">
                                    End Time:{" "}
                                  </p>
                                  <p className="mb-0">{data?.endTime}</p>
                                </span>
                              </div>
                            </div>
                          </Accordion.Header>
                          <Accordion.Body className="py-4">
                            {data?.specialtyLetter ? (
                              <>
                                <div className="d-flex justify-content-between">
                                  <h5 style={{ color: "#2269F2" }}>
                                    Specialist Letter
                                  </h5>
                                  <DropdownButton
                                    id="speciality-pdf"
                                    title={
                                      <BsThreeDotsVertical
                                        className="float-right color-99"
                                        size={25}
                                      />
                                    }
                                    size="sm"
                                    bsPrefix="moreIcon"
                                    className="moreIcon edit-dropdown"
                                    direction="left"
                                  >
                                    <div className="set-dropdown-menu">
                                      <Dropdown.Item className="d-flex align-items-center">
                                        <button
                                          className="bg-transparent border-0 p-0"
                                          onClick={() =>
                                            handleSpecialityFn(data)
                                          }
                                        >
                                          <PiDownloadSimpleBold size={20} />
                                          Download PDF
                                        </button>
                                      </Dropdown.Item>
                                    </div>
                                  </DropdownButton>
                                </div>
                                <span className="d-flex">
                                  <div className="custom-padding pe-5">
                                    <p
                                      style={{ color: "#999999" }}
                                      className="mb-1"
                                    >
                                      Formulation
                                    </p>
                                    <p>
                                      {data?.specialtyLetter?.specialistLetter
                                        ? data?.specialtyLetter
                                            ?.specialistLetter
                                        : "N/A"}
                                    </p>
                                  </div>
                                  <div className="d-flex custom-row">
                                    <div className="custom-padding">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        To
                                      </p>
                                      <p>
                                        {data?.specialtyLetter?.toDoctor
                                          ? data?.specialtyLetter?.toDoctor
                                          : "N/A"}
                                      </p>
                                    </div>
                                  </div>
                                </span>

                                <div>
                                  {data?.specialtyLetter?.appointmentDetail ? (
                                    <>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Presenting Complaint
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail
                                            ?.presentingComplaint
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.presentingComplaint
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Past Medical History
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail
                                            ?.pastMedicalHistory
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.pastMedicalHistory
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Medications
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail?.medications
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail?.medications
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Allergies
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail?.allergies
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail?.allergies
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Family History
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail?.familyHistory
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.familyHistory
                                            : "N/A"}
                                        </p>
                                      </div>

                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Social History
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail?.socialHistory
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.socialHistory
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Suspected Diagnosis
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail
                                            ?.suspectedDiagnosis
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.suspectedDiagnosis
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Recommended Action
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail
                                            ?.recommendedActions
                                            ? data?.specialtyLetter
                                                ?.appointmentDetail
                                                ?.recommendedActions
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Sign off
                                        </p>
                                        <p>
                                          {data?.specialtyLetter
                                            ?.appointmentDetail?.signOff
                                            ? data?.specialtyLetter
                                                ?.specialtyLetter
                                                ?.appointmentDetail?.signOff
                                            : "N/A"}
                                        </p>
                                      </div>
                                    </>
                                  ) : (
                                    <>
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        Referral Notes
                                      </p>
                                      <p>
                                        {data?.specialtyLetter?.referralNote
                                          ? data?.specialtyLetter?.referralNote
                                          : "N/A"}
                                      </p>
                                    </>
                                  )}
                                </div>
                              </>
                            ) : (
                              <p>No Speciality Letter Data</p>
                            )}
                            <hr />
                            {data?.emergenceyDepartmentLetter ? (
                              <>
                                <div className="d-flex justify-content-between">
                                  <h5
                                    style={{ color: "#E31E27" }}
                                    className="mb-4"
                                  >
                                    Emergency Department Letter
                                  </h5>
                                  <DropdownButton
                                    id="emergency-pdf"
                                    title={
                                      <BsThreeDotsVertical
                                        className="float-right color-99"
                                        size={25}
                                      />
                                    }
                                    size="sm"
                                    bsPrefix="moreIcon"
                                    className="moreIcon edit-dropdown"
                                    direction="left"
                                  >
                                    <div className="set-dropdown-menu">
                                      <Dropdown.Item className="d-flex align-items-center">
                                        <button
                                          className="bg-transparent border-0 p-0"
                                          onClick={() =>
                                            handleEmergencyFn(data)
                                          }
                                        >
                                          <PiDownloadSimpleBold size={20} />
                                          Download PDF
                                        </button>
                                      </Dropdown.Item>
                                    </div>
                                  </DropdownButton>
                                </div>
                                <span className="d-flex">
                                  <div className="custom-padding pe-5">
                                    <p
                                      style={{ color: "#999999" }}
                                      className="mb-1"
                                    >
                                      Emergency Letter
                                    </p>
                                    <p>
                                      {data?.emergenceyDepartmentLetter
                                        ?.specialistLetter
                                        ? data?.emergenceyDepartmentLetter
                                            ?.specialistLetter
                                        : "N/A"}
                                    </p>
                                  </div>
                                  <div className="d-flex custom-row">
                                    <div className="custom-padding">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        To
                                      </p>
                                      <p>
                                        {data?.emergenceyDepartmentLetter
                                          ?.toDoctor
                                          ? data?.emergenceyDepartmentLetter
                                              ?.toDoctor
                                          : "N/A"}
                                      </p>
                                    </div>
                                  </div>
                                </span>
                                <div>
                                  {data?.emergenceyDepartmentLetter
                                    ?.appointmentDetail ? (
                                    <>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Presenting Complaint
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail
                                            ?.presentingComplaint
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.presentingComplaint
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Past Medical History
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail
                                            ?.pastMedicalHistory
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.pastMedicalHistory
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Medications
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail?.medications
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail?.medications
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Allergies
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail?.allergies
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail?.allergies
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Family History
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail?.familyHistory
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.familyHistory
                                            : "N/A"}
                                        </p>
                                      </div>

                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Social History
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail?.socialHistory
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.socialHistory
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Suspected Diagnosis
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail
                                            ?.suspectedDiagnosis
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.suspectedDiagnosis
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Recommended Action
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail
                                            ?.recommendedActions
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail
                                                ?.recommendedActions
                                            : "N/A"}
                                        </p>
                                      </div>
                                      <div>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Sign off
                                        </p>
                                        <p>
                                          {data?.emergenceyDepartmentLetter
                                            ?.appointmentDetail?.signOff
                                            ? data?.emergenceyDepartmentLetter
                                                ?.appointmentDetail?.signOff
                                            : "N/A"}
                                        </p>
                                      </div>
                                    </>
                                  ) : (
                                    <>
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        Referral Notes
                                      </p>
                                      <p>
                                        {data?.emergenceyDepartmentLetter
                                          ?.referralNote
                                          ? data?.emergenceyDepartmentLetter
                                              ?.referralNote
                                          : "N/A"}
                                      </p>
                                    </>
                                  )}
                                </div>
                              </>
                            ) : (
                              <p>No Emergency Letter Data</p>
                            )}
                            <hr />
                            {data?.medicalCertificate ? (
                              <>
                                <div className="d-flex justify-content-between">
                                  <h5
                                    style={{ color: "#44BC19" }}
                                    className="mb-4"
                                  >
                                    Medical Certificate
                                  </h5>
                                  <DropdownButton
                                    id="medical-certificate-pdf"
                                    title={
                                      <BsThreeDotsVertical
                                        className="float-right color-99"
                                        size={25}
                                      />
                                    }
                                    size="sm"
                                    bsPrefix="moreIcon"
                                    className="moreIcon edit-dropdown"
                                    direction="left"
                                  >
                                    <div className="set-dropdown-menu">
                                      <Dropdown.Item className="d-flex align-items-center">
                                        <button
                                          className="bg-transparent border-0 p-0"
                                          onClick={() => handleMedicalFn(data)}
                                        >
                                          <PiDownloadSimpleBold size={20} />
                                          Download PDF
                                        </button>
                                      </Dropdown.Item>
                                    </div>
                                  </DropdownButton>
                                </div>
                                <span className="d-flex">
                                  <div className="custom-padding pe-5">
                                    <p
                                      style={{ color: "#999999" }}
                                      className="mb-1"
                                    >
                                      From
                                    </p>
                                    <p>
                                      {moment(
                                        data?.medicalCertificate?.fromDate
                                      )?.format("DD-MM-YYYY") || "N/A"}
                                    </p>
                                  </div>
                                  <div className="d-flex custom-row">
                                    <div className="custom-padding">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        To
                                      </p>
                                      <p>
                                        {moment(
                                          data?.medicalCertificate?.toDate
                                        )?.format("DD-MM-YYYY") || "N/A"}
                                      </p>
                                    </div>
                                  </div>
                                </span>
                                <div className="custom-padding">
                                  <p
                                    style={{ color: "#999999" }}
                                    className="mb-1"
                                  >
                                    Unable To Attend
                                  </p>
                                  <p>
                                    {data?.medicalCertificate
                                      ?.unabletoAttendField
                                      ? data?.medicalCertificate
                                          ?.unabletoAttendField
                                      : "N/A"}
                                  </p>
                                </div>
                                <div className="custom-padding">
                                  <p
                                    style={{ color: "#999999" }}
                                    className="mb-1"
                                  >
                                    Suffering From
                                  </p>
                                  <p>
                                    {data?.medicalCertificate?.sufferingFrom
                                      ? data?.medicalCertificate?.sufferingFrom
                                      : "N/A"}
                                  </p>
                                </div>
                              </>
                            ) : (
                              <p>No Medical Certificate Data</p>
                            )}
                          </Accordion.Body>
                        </Accordion.Item>
                      </Accordion>
                    </>
                  );
                })
              ) : (
                <p className="text-center">No Data</p>
              )}
            </div>
          </Card.Body>
        </Card>
      </div>
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <SpecialistLetterPDF stateData={singleDownLoad} />
      </PrintComponents>
      <PrintComponents
        trigger={
          <button
            ref={printButtonEmerRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <EmergencyLetterPDF stateData={emergencyData} />
      </PrintComponents>
      <PrintComponents
        trigger={
          <button
            ref={printButtonMedRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <MedicalCertificatePDF
          medicalCertificateData={medicalCertificateData?.medicalCertificate}
          doctorDetails={medicalCertificateData}
        />
      </PrintComponents>
    </Col>
  );
}

export default PatientReferrelNote;
