import React, { useEffect, useState } from "react";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { Container, Row, Col, Card, Form, Nav, Button } from "react-bootstrap";
import Tab from "react-bootstrap/Tab";
import { IoIosArrowForward } from "react-icons/io";
import { FaEdit } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import {
  deletePatientAccount,
  getPatient,
  updatePatient,
} from "../../../app/features/patient/patient.slice";
import moment from "moment";
import { useForm } from "react-hook-form";
import { FieldError } from "../../../components/ErrorMessage/ErrorMessage";
import PatientMedicalHistory from "./PatientComponent/PatientMedicalHistory";
import PatientNotes from "./PatientComponent/PatientNotes";
import PatientReferrelNote from "./PatientComponent/PatientReferrelNote";
import { getAllPatientNotes } from "../../../app/features/patientNotes/patientNotes.slice";
import PatientChangePassword from "./PatientComponent/PatientChangePassword";
import convertBase64 from "../../../components/GenericActions";
import AWSImageUpload from "../../../app/features/bucketImageUpload/bucketImageUpload.service";
import { logout, updatedProfile } from "../../../app/features/auth/auth.slice";
import {
  createPatientPharmacy,
  getAllPatientPharmacy,
  getPatientPharmacyData,
  updatePatientPharmacy,
} from "../../../app/features/patientPharmacy/patientPharmacy.slice";
import { ConfirmationModal } from "../../../components/Modal/ConfirmationModal";
import { getProfileNotes } from "../../../app/features/patientRefNotes/patientRefNotes.slice";

function Profile() {
  const [isEditing, setIsEditing] = useState(true);
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { getPatientData } = useSelector((state) => state.patient);
  const { allPatientNotesData } = useSelector((state) => state.patientNotes);
  const {profileNotesData} = useSelector((state) => state?.patientRefNotes);
  const [phoneValue, setPhoneValue] = useState(getPatientData?.phoneNumber);
  const [showPharmacyId, setShowPharmacyId] = useState();
  const [selectedPharmacy, setSelectedPharmacy] = useState({});
  const [open, setOpen] = useState(false);

  const [imageUrl, setImageUrl] = useState();
  const [loading, setLoading] = useState();
  const { patientPharmacy } = useSelector((state) => state);
  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm();

  const handleEditClick = () => {
    setIsEditing(false);
  };
  const handleEditSubmit = () => {
    setIsEditing(true);
  };

  /** s3 Bucket Image Upload */
  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);
      // convertBase64(file, (url) => {
      convertBase64(file).then((url) => {
        setLoading(true);
        const fileData = {
          name: name,
          base64: url,
          fileExtension: `${ext}`,
        };
        AWSImageUpload(fileData).then((response) => {
          if (response) {
            setImageUrl(response);
            setLoading(false);
          } else {
            // notification.error({
            //   message: response?.message,
            // });
          }
        });
      });
    }
  };
  /** Personal Information Patient */

  useEffect(() => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);
  const getPersonalInfo = () => {
    handleEditSubmit();
    const finalData = {
      patientId: user.userId,
    };
    dispatch(getPatient(finalData));
  };
  function moveToNext(data, newData) {
    const finalData = {
      pharmacyId: showPharmacyId ? showPharmacyId : 0,
      pharmacyName: newData?.pharmacyName,
      pharmacyLocation: newData?.pharmacyLocation,
      country: newData?.country,
      ig_xcord: "",
      ig_ycord: "",
      userId: user?.userId,
      pharmacyEmail: newData?.pharmacyEmail,
    };
    if (patientPharmacy?.patientPharmacyData?.data?.pharmacyId) {
      dispatch(updatePatientPharmacy({ finalData, getPersonalInfo }));
    } else {
      dispatch(createPatientPharmacy({ finalData, getPersonalInfo }));
    }

    const selectedPharmacyData = patientPharmacy?.allPatientPharmacyData?.find(
      (item) => item.pharmacyName === newData?.pharmacyName
    );
    if (selectedPharmacyData) {
      setValue("pharmacyLocation", selectedPharmacyData.pharmacyLocation);
      setValue("country", selectedPharmacyData.country);
      setSelectedPharmacy(selectedPharmacyData);
    }
  }
  function onSubmit(data) {
    const finalData = {
      patientId: user?.userId,
      firstName: data.firstName,
      lastName: data.lastName,
      image: imageUrl?.keyName
        ? imageUrl?.keyName
        : getPatientData?.image
        ? getPatientData?.image
        : "",
      dob: data.dob,
      genderId: +data.gender,
      patientLocalGP: data.patientLocalGP,
      phoneNumber: phoneValue
        ? phoneValue
        : getPatientData?.phoneNumber
        ? getPatientData?.phoneNumber
        : "",
      currentAddress: data.currentAddress,
      nextOfKinName: data.nextOfKinName,
      nextOfKinNumber: data.nextOfKinNumber,
      pharmacyId: showPharmacyId ? showPharmacyId : 0,
      pharmacyName: data.pharmacyName,
      pharmacyLocation: data?.pharmacyLocation,
      country: data?.country,
      pharmacyEmail: data?.pharmacyEmail,
    };
    const callBackFunc = () => {
      dispatch(updatedProfile());
    };
    dispatch(updatePatient({ finalData, moveToNext, callBackFunc }));
  }

  const notesAndReferralNotes = (data) => {
    const finalData = {
      noteTypeId: data,
      patientId: user?.userId,
    };

    dispatch(getAllPatientNotes(finalData));
  };

  function allPatientNotes() {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getProfileNotes(finalData))
  };


  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };

    dispatch(getPatient(finalData));
  }, [dispatch, user?.userId]);

  const logoutAccount = () => {
    debugger
    dispatch(logout());
  };

  function deleteAccount() {
    const finalData = {
      id: user?.userId,
    };
    dispatch(deletePatientAccount({finalData, logoutAccount}));

  }

  return (
    <div className="patient-profile">
      <div className="Profile_Main_Class">
        <Container fluid>
          <Tab.Container id="left-tabs-example" defaultActiveKey="first">
            <Row>
              <Col lg={3}>
                <Card className="card-height">
                  <Card.Body className="p-0">
                    <Nav variant="pills" className="flex-column ">
                      <Nav.Item>
                        <Nav.Link
                          eventKey="first"
                          className="personal-information ps-4 mt-4 personal-info-tabs"
                        >
                          <span className="Personal-Info d-flex justify-content-between align-items-center">
                            Personal Information
                            <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                          </span>
                        </Nav.Link>
                      </Nav.Item>
                      <hr className="horizontal-line" />
                      <Nav.Item>
                        <Nav.Link
                          eventKey="second"
                          className="personal-information ps-4 personal-info-tabs"
                        >
                          <span className="Personal-Info d-flex justify-content-between align-items-center">
                            Medical History
                            <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                          </span>
                        </Nav.Link>
                      </Nav.Item>
                      <hr className="horizontal-line" />
                      <Nav.Item>
                        <Nav.Link
                          eventKey="third"
                          className="personal-information ps-4 personal-info-tabs"
                          onClick={() => notesAndReferralNotes(1)}
                        >
                          <span className="Personal-Info d-flex justify-content-between align-items-center">
                            Notes
                            <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                          </span>
                        </Nav.Link>
                      </Nav.Item>
                      <hr className="horizontal-line" />
                      <Nav.Item>
                        <Nav.Link
                          eventKey="fourth"
                          className="personal-information ps-4 personal-info-tabs"
                          onClick={allPatientNotes}
                        >
                          <span className="Personal-Info d-flex justify-content-between align-items-center">
                            Referral Notes
                            <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                          </span>
                        </Nav.Link>
                      </Nav.Item>

                      <hr className="horizontal-line" />
                      <Nav.Item>
                        <Nav.Link
                          eventKey="fifth"
                          className="personal-information ps-4 personal-info-tabs"
                        >
                          <span className="Password-Sizing d-flex justify-content-between align-items-center">
                            Change Password
                            <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                          </span>
                        </Nav.Link>
                      </Nav.Item>
                      <hr className="horizontal-line" />
                      <Nav.Item>
                        <Nav.Link
                          className="personal-information ps-4"
                          onClick={() => setOpen(true)}
                        >
                          <span
                            style={{ color: "#E31E27" }}
                            className="Password-Sizing"
                          >
                            Delete Account
                          </span>
                        </Nav.Link>
                      </Nav.Item>
                    </Nav>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg={9}>
                <Card className="h-100">
                  <Card.Body className="p-0">
                    <Tab.Content className="p-0">
                      {isEditing ? (
                        <Tab.Pane eventKey="first">
                          <Col lg={12}>
                            <div>
                              <Card>
                                <Card.Img
                                  variant="top"
                                  className="Card-Image"
                                />
                                <Card.Body className="p-0">
                                  <Row>
                                    <Col md={9} xs={12}>
                                      <div className="d-flex align-items-center upload_pic">
                                        <img
                                          src={
                                            getPatientData?.imageUrl
                                              ? getPatientData?.imageUrl
                                              : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=6045eb&color=fff`
                                          }
                                          alt=""
                                          srcset=""
                                          className="img-fluid Profile-2 position-relative test-border"
                                        />
                                        <div
                                          style={{
                                            marginLeft: "28px",
                                            marginTop: "20px",
                                          }}
                                        >
                                          <p className="profile-header mb-0">
                                            {getPatientData?.firstName
                                              ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                                              : "N/A"}
                                          </p>
                                          <p className="profile-header-gender mb-0">
                                            {getPatientData?.gender
                                              ? `(${getPatientData?.gender})`
                                              : "(N/A)"}
                                          </p>
                                        </div>
                                      </div>
                                    </Col>

                                    <Col md={3} xs={12}>
                                      <button
                                        type="button"
                                        className="btn btn-light Edit-Button fw-bold text-decoration-none d-flex justify-content-center align-items-center"
                                        style={{ color: " #6045EB" }}
                                        onClick={handleEditClick}
                                      >
                                        <FaEdit className="me-3 fs-5" />
                                        <span className="fs-5 mb-0">Edit</span>
                                      </button>
                                    </Col>
                                  </Row>

                                  <Card.Title className="Personal-Information ms-5 ps-3 fw-bold">
                                    Personal Details
                                  </Card.Title>

                                  <Row className="ps-5 ">
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label">
                                        Name
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.firstName
                                          ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label">
                                        DOB
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.dob
                                          ? moment(getPatientData?.dob).format(
                                              "DD/MM/YYYY"
                                            )
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label">
                                        Gender
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.gender
                                          ? getPatientData?.gender
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Patient Local GP
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.patientLocalGP
                                          ? getPatientData?.patientLocalGP
                                          : "N/A"}
                                      </h5>
                                    </Col>

                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Email
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.email
                                          ? getPatientData?.email
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Phone Number
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.phoneNumber
                                          ? getPatientData?.phoneNumber
                                          : "N/A"}
                                      </h5>
                                    </Col>

                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Next of Kin Name
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.nextOfKinName
                                          ? getPatientData?.nextOfKinName
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Next of Kin Number
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.nextOfKinNumber
                                          ? getPatientData?.nextOfKinNumber
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Current Location
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.currentAddress
                                          ? getPatientData?.currentAddress
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Pharmacy Name
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.pharmacyName
                                          ? getPatientData?.pharmacyName
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Pharmacy Location
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.pharmacyLocation
                                          ? getPatientData?.pharmacyLocation
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-4">
                                        Country
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.country
                                          ? getPatientData?.country
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                    <Col lg={4} md={6} sm={12}>
                                      <Form.Label className="Name-Label mt-0">
                                        Pharmacy Email
                                      </Form.Label>
                                      <h5
                                        style={{ color: "#1A1A1A" }}
                                        className="Users-List fw-bold"
                                      >
                                        {getPatientData?.pharmacyEmail
                                          ? getPatientData?.pharmacyEmail
                                          : "N/A"}
                                      </h5>
                                    </Col>
                                  </Row>
                                </Card.Body>
                              </Card>
                            </div>
                          </Col>
                        </Tab.Pane>
                      ) : (
                        <Tab.Pane eventKey="first">
                          <Col lg={12}>
                            <div>
                              <Card>
                                <Form onSubmit={handleSubmit(onSubmit)}>
                                  <Card.Img
                                    variant="top"
                                    className="Card-Image"
                                  />
                                  <Card.Body className="p-0">
                                    <div className="upload_pic">
                                      <img
                                        src={
                                          imageUrl?.baseUrl ||
                                          getPatientData?.imageUrl ||
                                          `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=6045eb&color=fff`
                                        }
                                        alt=""
                                        className="img-fluid Profile-2 position-relative test-border"
                                      />
                                      <Button className="change-button" style={{marginTop: '4rem'}}>
                                        <label htmlFor="patient-pic">
                                          Change
                                        </label>
                                        <Form.Control
                                          loading={loading}
                                          onChange={(e) =>
                                            handleChangeUpload(e)
                                          }
                                          type="file"
                                          id="patient-pic"
                                          name="patientPicture"
                                          accept="image/*"
                                        />
                                      </Button>
                                      <Button
                                        className="remove-button border-0"
                                        style={{marginTop: '4rem'}}
                                        onClick={() => setImageUrl("")}
                                      >
                                        Remove
                                      </Button>
                                    </div>

                                    <Card.Title className="Personal-Information ms-5 ps-3 fw-bold">
                                      Personal Information
                                    </Card.Title>

                                    <Row className="ps-4 mt-5">
                                      <Col
                                        xl={4}
                                        lg={6}
                                        md={6}
                                        sm={12}
                                        className=""
                                      >
                                        <Form.Label
                                          className="Name-Label"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          First Name
                                        </Form.Label>
                                        <Form.Control
                                          name="firstName"
                                          type="text"
                                          maxLength={50}
                                          placeholder="First Name"
                                          className="Input-Height"
                                          {...register("firstName")}
                                          defaultValue={
                                            getPatientData?.firstName
                                          }
                                        />
                                        {errors.firstName && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col
                                        xl={4}
                                        lg={6}
                                        md={6}
                                        sm={12}
                                        className=""
                                      >
                                        <Form.Label
                                          className="Name-Label"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Last Name
                                        </Form.Label>
                                        <Form.Control
                                          name="lastName"
                                          type="text"
                                          maxLength={50}
                                          placeholder="Last Name"
                                          className="Input-Height"
                                          {...register("lastName")}
                                          defaultValue={
                                            getPatientData?.lastName
                                          }
                                        />
                                        {errors.lastName && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col
                                        xl={4}
                                        lg={6}
                                        md={6}
                                        sm={12}
                                        className=""
                                      >
                                        <Form.Label
                                          className="Name-Label"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          D.O.B
                                        </Form.Label>
                                        <Form.Control
                                          name="dob"
                                          type="date"
                                          placeholder="Select Gender"
                                          className="Input-Height"
                                          {...register("dob")}
                                          defaultValue={moment(
                                            getPatientData?.dob
                                          )?.format("YYYY-MM-DD")}
                                          max={moment(new Date()).format(
                                            "YYYY-MM-DD"
                                          )}
                                        />
                                        {errors.dob && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label ps-0 pe-0 mt-4    "
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Gender
                                        </Form.Label>
                                        <Form.Select
                                          name="gender"
                                          className="Input-Height Select-Dropdown-Sizing"
                                          {...register("gender")}
                                          defaultValue={getPatientData?.gender}
                                        >
                                          <option value={101}>Male</option>
                                          <option value={102}>Female</option>
                                          <option value={103}>Other</option>
                                        </Form.Select>
                                        {errors.gender && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Patient Local GP
                                        </Form.Label>
                                        <Form.Control
                                          name="patientLocalGP"
                                          type="text"
                                          maxLength={50}
                                          placeholder="Patient Local GP"
                                          className="Input-Height"
                                          {...register("patientLocalGP")}
                                          defaultValue={
                                            getPatientData?.patientLocalGP
                                          }
                                        />
                                        {errors.patientLocalGP && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Email
                                        </Form.Label>
                                        <Form.Control
                                          name="email"
                                          type="email"
                                          maxLength={50}
                                          disabled
                                          placeholder="patient@gmail.com"
                                          className="Input-Height"
                                          {...register("email", {
                                            pattern:
                                              /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                                          })}
                                          defaultValue={getPatientData?.email}
                                        />
                                        {errors.email && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col
                                        xl={4}
                                        lg={6}
                                        md={6}
                                        sm={12}
                                        className=""
                                      >
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Phone Number
                                        </Form.Label>
                                        <PhoneInput
                                          className="Phone-Number-FieldWidth border rounded"
                                          placeholder="8945 898 898"
                                          value={getPatientData?.phoneNumber}
                                          onChange={(e) => setPhoneValue(`+${e}`)}
                                          maxLength={15}
                                        />
                                        {errors.phoneNumber && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>

                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Next of Kin Name
                                        </Form.Label>
                                        <Form.Control
                                          name="nextOfKinName"
                                          type="text"
                                          maxLength={100}
                                          placeholder="Next of Kin Name"
                                          className="Input-Height"
                                          {...register("nextOfKinName")}
                                          defaultValue={
                                            getPatientData?.nextOfKinName
                                          }
                                        />
                                        {errors.nextOfKinName && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Next of Kin Number
                                        </Form.Label>
                                        <Form.Control
                                          name="nextOfKinNumber"
                                          type="text"
                                          maxLength={100}
                                          placeholder="Next of Kin Number"
                                          className="Input-Height"
                                          {...register("nextOfKinNumber")}
                                          defaultValue={
                                            getPatientData?.nextOfKinName
                                          }
                                        />
                                        {errors.nextOfKinNumber && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>

                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <div className="d-flex justify-content-between align-items-baseline mt-4">
                                          <span className="w-100">
                                            <Form.Label className="fw-bold">
                                              Pharmacy Name
                                            </Form.Label>
                                            <Form.Group>
                                              <Form.Control
                                                list="pharmacyNameList"
                                                className="Input-Height"
                                                defaultValue={
                                                  getPatientData?.pharmacyName ??
                                                  ""
                                                }
                                                {...register("pharmacyName")}
                                                onChange={(e) => {
                                                  const selectedPharmacyData =
                                                    patientPharmacy?.allPatientPharmacyData?.find(
                                                      (item) =>
                                                        item.pharmacyName ===
                                                        e.target.value
                                                    );

                                                  if (selectedPharmacyData) {
                                                    setValue(
                                                      "pharmacyLocation",
                                                      selectedPharmacyData.pharmacyLocation
                                                    );
                                                    setValue(
                                                      "country",
                                                      selectedPharmacyData.country
                                                    );
                                                    setSelectedPharmacy(
                                                      selectedPharmacyData
                                                    );
                                                    setShowPharmacyId(
                                                      selectedPharmacyData.pharmacyId
                                                    );
                                                  } else {
                                                    setValue(
                                                      "pharmacyLocation",
                                                      ""
                                                    );
                                                    setValue("country", "");
                                                    setSelectedPharmacy({});
                                                  }
                                                }}
                                                placeholder="Select Pharmacy Name"
                                                size="lg"
                                              />
                                              <datalist id="pharmacyNameList">
                                                {patientPharmacy?.allPatientPharmacyData?.map(
                                                  (item) => (
                                                    <option
                                                      key={item?.pharmacyId}
                                                      value={item?.pharmacyName}
                                                    >
                                                      {item.pharmacyName}
                                                    </option>
                                                  )
                                                )}
                                              </datalist>
                                            </Form.Group>
                                          </span>
                                        </div>
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Group>
                                          <Form.Label className="fw-bold mt-4">
                                            Pharmacy Address
                                          </Form.Label>
                                          <Form.Control
                                            type="text"
                                            size="lg"
                                            placeholder="Select Address"
                                            className="Input-Height"
                                            {...register("pharmacyLocation")}
                                            defaultValue={
                                              getPatientData?.pharmacyLocation
                                                ? getPatientData?.pharmacyLocation
                                                : ""
                                            }
                                            maxLength={250}
                                          />
                                        </Form.Group>
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Group>
                                          <Form.Label className="fw-bold mt-4">
                                            Country
                                          </Form.Label>
                                          <Form.Control
                                            className="Input-Height"
                                            type="text"
                                            size="lg"
                                            placeholder="Country"
                                            {...register("country")}
                                            defaultValue={
                                              getPatientData?.country ?? ""
                                            }
                                            maxLength={250}
                                          />
                                        </Form.Group>
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Group>
                                          <Form.Label className="fw-bold mt-4">
                                            Pharmacy Email
                                          </Form.Label>
                                          <Form.Control
                                            type="text"
                                            size="lg"
                                            placeholder="Pharmacy Email"
                                            className="Input-Height"
                                            {...register("pharmacyEmail")}
                                            defaultValue={
                                              getPatientData?.pharmacyEmail
                                                ? getPatientData?.pharmacyEmail
                                                : ""
                                            }
                                            maxLength={250}
                                          />
                                        </Form.Group>
                                      </Col>
                                      <Col xl={4} lg={6} md={6} sm={12}>
                                        <Form.Label
                                          className="Name-Label mt-4"
                                          style={{
                                            color: "#1A1A1A",
                                            fontWeight: "bold",
                                          }}
                                        >
                                          Current Location
                                        </Form.Label>
                                        <Form.Control
                                          name="currentAddress"
                                          type="text"
                                          maxLength={100}
                                          placeholder="4517 Washington Ave. Manchester, Kentucky 39495"
                                          className="Input-Height"
                                          {...register("currentAddress")}
                                          defaultValue={
                                            getPatientData?.currentAddress
                                          }
                                        />
                                        {errors.currentAddress && (
                                          <FieldError
                                            message={"This Field is Required"}
                                          />
                                        )}
                                      </Col>
                                    </Row>
                                    <div className="mt-3 d-flex justify-content-end">
                                      <Button
                                        onClick={() => setIsEditing(true)}
                                        className="my-2 Save-Changes-Btn"
                                        style={{
                                          background: "#eae5e5",
                                          borderColor: "#eae5e5",
                                          color: "#000",
                                        }}
                                      >
                                        Cancel
                                      </Button>
                                      <Button
                                        className="my-2 Save-Changes-Btn"
                                        style={{ background: "#6045EB" }}
                                        type="submit"
                                      >
                                        Save Changes
                                      </Button>
                                    </div>
                                  </Card.Body>
                                </Form>
                              </Card>
                            </div>
                          </Col>
                        </Tab.Pane>
                      )}

                      <Tab.Pane eventKey="second">
                        <PatientMedicalHistory
                          user={user}
                          getPatientData={getPatientData}
                        />
                      </Tab.Pane>

                      <Tab.Pane eventKey="third">
                        <PatientNotes
                          allPatientNotesData={allPatientNotesData}
                          getPatientData={getPatientData}
                        />
                      </Tab.Pane>

                      <Tab.Pane eventKey="fourth">
                        <PatientReferrelNote
                          profileNotesData={profileNotesData}
                          getPatientData={getPatientData}
                        />
                      </Tab.Pane>

                      <Tab.Pane eventKey="fifth" data-toggle="tab">
                        <PatientChangePassword
                          user={user}
                          getPatientData={getPatientData}
                        />
                      </Tab.Pane>
                    </Tab.Content>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Tab.Container>
        </Container>
      </div>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Account"}
        title={"your account"}
        removeFunc={deleteAccount}
      />
    </div>
  );
}

export default Profile;